
@tailwind base;
@tailwind components;
@tailwind utilities;

body{
background: rgb(244, 243, 243);
}

html {
  scroll-behavior: smooth;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes shine {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}
